@font-face {
    font-family: 'Orith Display';
    src:
            url('./assets/fonts/OrithDisplay-Regular.ttf') format('ttf'),
            url('./assets/fonts/OrithDisplay-Regular.otf') format('otf');
    font-weight: normal;
    font-style: normal;
}

/*ALL*/
* {
    margin: 0;
    padding: 0;
    border: none;
    box-sizing: border-box;
    font-family: "Montserrat", sans-serif;
    text-decoration: none;
    outline: none;
    transition: all 0.3s ease-in-out;
}

body {
    width: 100vw;
    height: fit-content;

    position: relative;

    transition: none;
    overflow-x: hidden;
}

body .background {
    width: 100vw;
    height: 100%;

    position: absolute;
    top: 0;
    left: 0;

    transition: none;

    object-fit: fill;
    z-index: -1000;
}

html::-webkit-scrollbar-track  {
    border-radius: 10px;
    background-color: rgba(245, 245, 245, 0);
}

html::-webkit-scrollbar  {
    width: 8px;
    height: 8px;
    background-color: rgba(245, 245, 245, 0);
}

html::-webkit-scrollbar-thumb  {
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.4);
}

@media(max-width: 500px) {
    html::-webkit-scrollbar  {
        width: 4px;
        height: 4px;
        background-color: rgba(245, 245, 245, 0);
    }
}

img {
    cursor: pointer;
}